@keyframes callbackIcon {
  0% {
    transform: scale(0); }
  25% {
    transform: scale(1.5); }
  50% {
    transform: scale(0.7); }
  100% {
    transform: scale(1); } }

.LoginForm fieldset {
  border: 0; }

.LoginForm .feedback {
  font-size: 1.2rem;
  color: #6200ff;
  margin: 0.5rem 0;
  text-transform: uppercase; }

.LoginForm .formName {
  border-left: 6px solid #6200ff;
  padding-left: 3rem;
  margin-left: -3rem;
  margin-bottom: 3rem;
  color: #6200ff;
  font-size: 3rem;
  font-weight: 300;
  text-transform: uppercase; }

.LoginForm .SubmitButton {
  background-color: #6200ff;
  color: #fff;
  border: 0;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1.6rem;
  padding: 1rem 2rem;
  cursor: pointer;
  outline: none; }
  .LoginForm .SubmitButton:hover {
    background-color: #5800e6; }



