.App {
  width: 100%;
  max-width: 40rem;
  background-color: #fff;
  padding: 3rem;
  color: #818181;
  position: relative;
  -webkit-box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.3);
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.3); }
  .App::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    -webkit-transform: scale(0.9) translateY(-2rem);
    -ms-transform: scale(0.9) translateY(-2rem);
    transform: scale(0.9) translateY(-2rem);
    -webkit-transform-origin: 50% 0;
    -ms-transform-origin: 50% 0;
    transform-origin: 50% 0;
    opacity: 0.5;
    background-color: inherit; }
  .App::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    -webkit-transform: scale(0.8) translateY(-4rem);
    -ms-transform: scale(0.8) translateY(-4rem);
    transform: scale(0.8) translateY(-4rem);
    -webkit-transform-origin: 50% 0;
    -ms-transform-origin: 50% 0;
    transform-origin: 50% 0;
    opacity: 0.5;
    background-color: inherit; }



