@keyframes callbackIcon {
  0% {
    transform: scale(0); }
  25% {
    transform: scale(1.5); }
  50% {
    transform: scale(0.7); }
  100% {
    transform: scale(1); } }

.LoginSuccess {
  text-align: center; }
  .LoginSuccess img {
    -webkit-animation: callbackIcon 1s;
    animation: callbackIcon 1s; }
  .LoginSuccess h3 {
    font-weight: 300;
    font-size: 2.5rem; }



