@keyframes callbackIcon {
  0% {
    transform: scale(0); }
  25% {
    transform: scale(1.5); }
  50% {
    transform: scale(0.7); }
  100% {
    transform: scale(1); } }

.Field {
  margin-bottom: 2rem; }
  .Field .InputField {
    border-bottom: 2px solid #000000;
    position: relative; }
    .Field .InputField::before {
      content: '';
      position: absolute;
      bottom: -2px;
      left: 0;
      border-bottom: 2px solid #6200ff;
      -webkit-transition: width 300ms;
      -o-transition: width 300ms;
      transition: width 300ms;
      width: 0; }
    .Field .InputField .input-font, .Field .InputField label, .Field .InputField input {
      font-size: 1.6rem;
      color: #000000; }
    .Field .InputField label {
      display: block;
      position: relative;
      -webkit-transition: all 300ms;
      -o-transition: all 300ms;
      transition: all 300ms;
      font-size: 1.2rem;
      line-height: 2rem; }
    .Field .InputField input {
      display: block;
      width: 100%;
      border: 0;
      height: 2rem;
      outline: none; }
    .Field .InputField.focus label,
    .Field .InputField.focus input {
      color: #6200ff; }
    .Field .InputField.focus::before {
      width: 100%; }
    .Field .InputField.placeholder label {
      font-size: 1.6rem;
      -webkit-transform: translateY(2rem);
      -ms-transform: translateY(2rem);
      transform: translateY(2rem);
      z-index: 0; }
  .Field .CheckboxField label {
    display: block;
    line-height: 1rem;
    position: relative; }
    .Field .CheckboxField label::before {
      content: '';
      display: inline-block;
      width: 1rem;
      height: 1rem;
      border: 2px solid #000000;
      margin-right: 1rem;
      -webkit-transition: background-color 500ms;
      -o-transition: background-color 500ms;
      transition: background-color 500ms; }
    .Field .CheckboxField label::after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      display: inline-block;
      width: 1.4rem;
      height: 1.4rem;
      background-color: #000000;
      border-radius: 100%;
      -webkit-transform: scale(0);
      -ms-transform: scale(0);
      transform: scale(0);
      -webkit-transition: opacity 500ms;
      -o-transition: opacity 500ms;
      transition: opacity 500ms; }
  .Field .CheckboxField input {
    display: none; }
    .Field .CheckboxField input:checked + label::before {
      background-color: #000000; }
    .Field .CheckboxField input:checked + label::after {
      -webkit-animation: callbackIcon 1s;
      animation: callbackIcon 1s;
      opacity: 0; }
  .Field .error {
    font-size: 1.2rem;
    color: #ff0000;
    margin: 0.5rem 0;
    text-transform: uppercase; }



